.banner {
  position: relative;
  overflow: hidden;
}
.banner-view {
  position: relative;
  height: 100%;
  /*background-color: #090b22;*/
  background-color: lightcyan;
  background-repeat: no-repeat;
}
.banner-view i {
  position: relative;
  display: block;
  float: left;
  background-repeat: no-repeat;
}
.banner-btn {
  position: absolute;
  width: 100%;
  height: 0;
  top: 45%;
  font-family: "\5B8B\4F53";
  font-size: 20px;
  z-index: 1000;
}
.banner-btn span {
  display: block;
  float: left;
  width: 50px;
  line-height: 50px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
  color: #74dcff;
  cursor: pointer;
  font-weight: 800;
  border-radius: 50%;
  margin: 0 20px;
}
.banner-btn span:hover {
  background-color: rgba(0, 0, 0, 0.6);
}
.banner-btn span + span {
  float: right;
}
.banner-number {
  position: absolute;
  bottom: 35px;
  width: 100%;
  height: 0;
  font-size: 0;
  text-align: center;
  z-index: 1000;
}
.banner-number > * {
  display: inline-block;
  border: 2px solid #fff;
  border-radius: 50%;
  margin: 0 8px;
  width: 10px;
  height: 10px;
  background-color: #00c3ff;
  cursor: pointer;
}
.banner-number > *:hover,
.banner-number > *.on {
  background-color: #ffc300;
}
.banner-progres {
  width: 100%;
  position: absolute;
  bottom: 0;
  height: 3px;
  z-index: 1000;
}
.banner-progres i {
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 3px;
  display: block;
  height: 100%;
  width: 0;
}
